<template>
    <div class="myPhonelogin">
         <van-nav-bar
            :border='false'
            >
            <template #left>
                 <span  class="text"  >
                     <span class="iconfont icon-arrow-fine-up-copy" @click="onClickLeft"></span> 
                     手机登录
                </span>
            </template>
            </van-nav-bar>

            <p>未注册手机号登陆后将自动创建帐号</p>
            <van-field v-model="tel" type="tel" label="+86" >
                <template #button>
                    <van-icon name="cross" v-if="isShow" @click="ff_clears"/>
                </template>
            </van-field>

           
            <van-button class="nexts" round block type="info" native-type="submit" :color="isColor ? '#fe3a3b' :  '#ff7f7e'" @click='ff_tast' @keyup.enter='ff_tast'>
                下一步
            </van-button>
    </div>
</template>

<script>
   import {inspectFrom} from '../assets/js/inspectFrom/inspectFrom.js'
    export default {
        name : 'Phonelogin',
        data() {
            return {
                tel     : '',
                isShow  : false,
                isColor : false
            }
        },
        methods:{
            onClickLeft(){
                this.$router.go(-1);
                
            },
            ff_clears(){
                this.tel = ''
            },
            ff_tast(){
                let obj = {
                    phone:{
                        value    :  this.tel,
                        errorMsg :  '手机号格式错误',
                        reg      :  /^1[3-9]\d{9}$/
                    },
                }

                let isTast = inspectFrom.inspect(obj);
                
                if(isTast){
                   
                    this.axios({
                        url:"/cellphone/existence/check",
                        method:'GET',
                        params:{
                            phone:this.tel
                        }
                    })
                    .then(res=>{
                        console.log(res.data);
                        if(res.data.hasPassword){
                            this.$router.push({name:'Passlogin',query:{nickname:res.data.nickname,phone:this.tel}});
                        }else{
                            this.$router.push({name:'Authlogin',query:{nickname:res.data.nickname,phone:this.tel}});
                        }
                        
                    })
                    .catch(err=>{
                        console.log(err)
                    })
                }
            }
        },
        watch: {
            tel(val){
                if(val.length>0){
                     this.isShow = true;
                }else{
                     this.isShow = false;
                }
                if(val.length==11){
                     this.isColor = true;
                }else{
                     this.isColor = false;
                }
            }
        },
    }
</script>

<style lang="less" scoped>
     .myPhonelogin{

         /deep/.van-nav-bar__text{
             color: #000;
             font-size: 16px;
             font-weight: 550;
               
         }
         /deep/.van-nav-bar__content{
             height: 75px;
         }
        .text{
                font-size: 0.45333rem;
                font-weight: 800;
                -webkit-transform: scale(0.9);
                font-size: 22px;
                transform: scale(0.9);
                margin-left: 0.26667rem;
                display: inline-block;
                font-family: '宋体';


                .iconfont{
                    font-size: 20px;
                    margin-left: -18px;
                }
             }
        >p{
            text-align: left;
            font-size: 13px;
            color: #979797;
            box-sizing: border-box;
            padding: 0px 15px;

        }
        /deep/.van-field__label{
                color: #646566;
                text-align: left;
                font-size: 21px;
                font-weight: 548;
        }
        /deep/.van-field__label{
            width: 40px;
        }
        /deep/.van-cell{
            padding: 10px 0px;
            width: 92%;
            margin: 0px auto;
            border-bottom: 1px solid #e7e7e7;
            box-sizing: border-box;
            position: relative;
        }
        /deep/.van-icon{
            font-size: 20px;
            position: absolute;
            right: 3px;
            top: 2px;

        }
        .nexts{
            width: 93%;
            height: 45px;
            line-height: 45px;
            font-weight: 500;
            font-size: 18px;
            text-align: center;
            color: #fff;
           
            border-radius: 25px;
            margin: 40px auto;
            letter-spacing: 3px;
        }

     }

</style>